const actions = {
  getTopItemReports: (page, search, filter, filter_select, based) => ({
    type: "GET_TOP_ITEM_REPORTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter,
      filter_select,
      based: based //sort_by: quantity based or amount based
    }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
